@import "./scss/config/index.scss";
.group {
  width: 100%;
  display: inline-flex;
  height: 60px;
  margin-bottom: 40px;
  box-shadow: rgba(#000, 0.08) 0 3px 9px 0;
  border-radius: 6px;
  text-align: center;
}

.input {
  width: 100%;
  text-align: left;
}

.button {
  height: 100%;
  width: 125px;
  font-size: 16px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}



