@import "./scss/config/index.scss";

@keyframes errorAppend{
  0% {
    max-height: 0;
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    max-height: 300px;
  }
}

@mixin errAppend {
  animation: errorAppend 0.7s linear 1;
  will-change: max-height, opacity;
}

.sellGroup {
  position: relative;
  z-index: 10;
}

.wrapper {
  width: 100%;
  margin: 0 auto;

  &.--dark {
    .togleText {
      color: #8e9aa3;
    }
  }
}

.exchangeRate {
  margin: 10px 0;
  input {
    border: none;
    border-radius: 0;
    border-right: 1px solid rgba(0, 0, 0, 0.2);
    height: 66px;
  }
}

.togleText {
  position: relative;
  top: -35px;
  left: 65px;
}

.togles {
  height: 40px;
}

.controlsToggles {
  margin-bottom: 9px;
}

@include media-tablet-landscape-and-lower  {
  .wrapper {
    width: 320px;
    margin: 0 auto;
  }
}

.button {
  margin-top: 15px;
  width: 100%;
  background: #000;
  border-radius: 3px;
  height: 68px;
  font-size: 16px;
  flex-basis: 47%;
  box-shadow: none;
}

.button:disabled {
  background: #F5F5F5;
}

.togleGroup {
  display: inline-block;
}

.error {
  margin-top: 8px;
  font-size: 14px;
  color: $color-f-error;

  [role = "errorLink"] {
    display: inline !important;
    color: inherit !important;
    vertical-align: inherit !important;
    text-decoration: underline !important;
  }
}

.Error {
  display: flex;
  align-items: center;
  color: #fff;
  padding: 0.3em 10px;
  margin-top: -5px;
  font-size: 13px;
  background: #FD6B65;
  border-radius: 3px;
  margin-bottom: 15px;
  @include errAppend();
  a {
    color: inherit;
    font-size: inherit;
    text-decoration: underline;
  }
}


@include media-mobile {
  .addOffer {
    width: 100%;
    .error {
      span:nth-child(1) {
        margin-right: 5px;
      }
      span:nth-child(2){
        display: block;
      }
    }
    .exchangeRate {
      margin: 15px 0;
    }
  }
}



